@import url(https://fonts.googleapis.com/css2?family=Open+Sans+Condensed&display=swap);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.menu-item {
  min-width: 30%;
  height: 240px;
  flex: 1 1 auto;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid black;
  margin: 0 7.5px 15px;
  overflow: hidden; }
  .menu-item:hover {
    cursor: pointer; }
    .menu-item:hover .background-image {
      -webkit-transform: scale(1.1);
              transform: scale(1.1);
      transition: -webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
      transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95);
      transition: transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95), -webkit-transform 6s cubic-bezier(0.25, 0.45, 0.45, 0.95); }
    .menu-item:hover .content {
      opacity: 0.9; }
  .menu-item.large {
    height: 580px; }
  .menu-item:first-child {
    margin-right: 7.5px; }
  .menu-item:last-child {
    margin-left: 7.5px; }
  .menu-item .background-image {
    width: 100%;
    height: 100%;
    background-position: center;
    background-size: cover; }
  .menu-item .content {
    height: 90px;
    padding: 0 25px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    border: 1px solid black;
    background-color: #ffffff;
    opacity: 0.7;
    position: absolute; }
    .menu-item .content .title {
      font-weight: bold;
      margin-bottom: 6px;
      font-size: 22px;
      color: #4a4a4a; }
    .menu-item .content .subtitle {
      font-weight: lighter;
      font-size: 16px; }

.directory-menu {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

.homepage {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 20px 80px; }

.collection-item {
  width: 22%;
  display: flex;
  flex-direction: column;
  height: 350px;
  align-items: center; }
  .collection-item .image {
    width: 100%;
    height: 95%;
    background-size: cover;
    background-position: center;
    margin-bottom: 5px; }
  .collection-item .collection-footer {
    width: 100%;
    height: 5%;
    display: flex;
    justify-content: space-between;
    font-size: 18px; }

.collection-preview {
  display: flex;
  flex-direction: column;
  margin-bottom: 30px; }
  .collection-preview title {
    font-size: 20px;
    margin-bottom: 25px; }
  .collection-preview .preview {
    display: flex;
    justify-content: space-between; }

.header {
  height: 70px;
  width: 100%;
  display: flex;
  justify-content: space-between;
  margin-bottom: 25px; }
  .header .logo-container {
    padding: 5px; }
    .header .logo-container .logo {
      width: 80px;
      -webkit-filter: grayscale(100%);
              filter: grayscale(100%); }
  .header .options {
    width: 50%;
    height: 100%;
    display: flex;
    align-items: center;
    justify-content: flex-end; }
    .header .options .option {
      padding: 10px 15px; }


